
html, body, #app {
  height: 100%;
}
.container {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    /*background-color: #eee;*/
}
@media (max-width: 768px) {
.pure-input-1-2 {
    width: 100%!important;
}
}
@media (min-width: 768px) {
.container {
        padding-right: 15px;
        padding-left: 15px;
        width: 750px;
}
}
@media (min-width: 992px) {
.container {
        width: 970px;
}
}
@media (min-width: 1200px) {
.container {
        width: 1170px;
}
}
.iframe {
  width: 100%;
  height: 90%;
  margin-top: 20px;
}
.button-success,
.button-warning {
    color: white;
    border-radius: 4px;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.button-success {
    background: rgb(28, 184, 65); /* this is a green */
}
.button-warning {
    background: rgb(223, 117, 20); /* this is an orange */
}



